import {useContext} from "react";
import {PostContext, PostProvider} from "../Main";
import {Link} from "react-router-dom";
import {Layout} from "./BlogPost";


function Blog() {
    return (
        <PostProvider>
            <Layout>
                <BlogPosts/>
            </Layout>
        </PostProvider>
    )
}

function BlogPosts() {
    const posts = useContext(PostContext);
    return (
        <div className="container">
            <div className="row g-2"> {/* Apply gap here to the row */}
                {posts.map((post) => (
                    <div className="col-12 mb-4" key={post.id}> {/* Wrap Post in a col for proper grid layout */}
                        <Post post={post}/>
                    </div>
                ))}
            </div>
        </div>
    )
}

function Post({post}) {
    return (
        <div className="row post rounded border-0" style={{border: "1px solid #fff", backgroundColor: "#fff"}}>
            <Link to={`/posts/${post.path}`} className="no-decoration">
                <h3>{post.title}</h3> {/* Display title */}
                <p>{post.summary}</p> {/* Display summary */}
                <p><strong>{post.date.toLocaleDateString()}</strong></p> {/* Display formatted date */}
            </Link> {/* Link to the full post */}
        </div>
    )
}

export default Blog;
