import Project from "./project";
import {PostContext} from "./Main";
import {useContext} from "react";

function Projects({nPosts}){
    const posts = useContext(PostContext);
    console.log(nPosts);
    return (
        <div className = "mt-4">
            <h2>Some recent projects</h2>
            <div className={"card-group column-gap-4 mt-4"}>
                {posts.slice(0, nPosts).map((post) => (
                    <Project post = {post}/>
                ))}
            </div>
        </div>
    )
}

export default Projects;