import logoLight from './assets/images/sun.svg';
import logoDark from './assets/images/moon.svg';
import {Link, useLocation} from "react-router-dom";
import {useEffect, useState} from "react";

function Header() {
    const savedTheme = localStorage.getItem("theme") || "dark";
    const [theme, setTheme] = useState(savedTheme);
    const logo = theme === "dark" ? logoLight : logoDark;

    useEffect(() => {
        const savedTheme = localStorage.getItem("theme");
        if (savedTheme) {
            setTheme(savedTheme);
        }
    }, []);

    useEffect(() => {
        document.body.className = theme;
        localStorage.setItem("theme", theme);
    }, [theme]);

    function changeTheme() {
        setTheme((prev) => (prev === "dark" ? "light" : "dark"));

    }

    return (
        <header className="header container mb-5">
            <ul>
                <li><Link to="/" className='no-decoration link-hover'>Sven Glinz </Link></li>
                <li><Link to="/blog" className='no-decoration link-hover'>Blog</Link></li>
                <li><Link to="/cv" className='no-decoration link-hover'>CV</Link></li>
            </ul>
            <ul>
                <img src={logo} onClick={changeTheme} alt="" className="toggle-icon"/>
            </ul>
        </header>
    )
}

export default Header;
