import {useLocation, useParams} from "react-router-dom";
import ReactMarkdown from 'react-markdown';
import {useEffect, useState} from "react";
import Header from "../header";
import Footer from "../Footer";
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { coy, dracula } from 'react-syntax-highlighter/dist/esm/styles/prism'; // Choose a theme

// use this everywhere ! (maybe improve a bit, especially since main is outside of header ?? and conent
// wrapped inside div with class main???

export function Layout({children}) {
    return (
        <>
            <main>
                <Header/>
                <div className={"main container"}>
                        {children}
                </div>
                <Footer/>
            </main>
        </>
    )
}

function BlogPost() {
    const {path} = useParams();

    const [markdown, setMarkdown] = useState('');

    useEffect(() => {
        const fetchMarkdown = async () => {

            // Replace with your actual path to the Markdown file
            const response = await fetch(`/posts/${path}/index.md`);
            console.log(response);
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const text = await response.text();
            setMarkdown(text);
        };
        fetchMarkdown();
    }, [path]);

    return (
        <Layout>
            <div className="post-content w-100">
                <ReactMarkdown
                    children={markdown}
                    components={{
                        code({ node, inline, className, children, ...props }) {
                            const match = /language-(\w+)/.exec(className || '');
                            return !inline && match ? (
                                <SyntaxHighlighter
                                    children={String(children).replace(/\n$/, '')}
                                    style={dracula}
                                    language={match[1]}
                                    PreTag="div"
                                    {...props}
                                />
                            ) : (
                                <code className={className} {...props}>
                                    {children}
                                </code>
                            );
                        },
                    }}
                />
            </div>
        </Layout>
    );
}

export default BlogPost;